function getDefaultCategoryAxis(text = "Category Axis") {
    const title = getDefaultChartText({
        text,
        visible: true,
        font: getDefaultFont(TextElements.AxisTitle),
    });
    title.content.horizontalAlignment = RESTHorizontalAlignment.Center;
    title.content.verticalAlignment = RESTVerticalAlignment.Middle;
    const labels = getDefaultChartText({
        text,
        visible: true,
        font: getDefaultFont(TextElements.AxisLabels),
    });
    return {
        type: WebChartTypes.Axis,
        visible: true,
        title,
        labels,
        valueFormat: getDefaultCategoryFormat(),
        lineSymbol: getDefaultLineSymbol({ color: defaultAxisLineColor }),
    };
}
export function getDefaultValueAxis(text = "Value Axis") {
    const title = getDefaultChartText({
        text,
        visible: true,
        font: getDefaultFont(TextElements.AxisTitle),
    });
    const labels = getDefaultChartText({
        text,
        visible: true,
        font: getDefaultFont(TextElements.AxisLabels),
    });
    title.content.verticalAlignment = RESTVerticalAlignment.Middle;
    title.content.horizontalAlignment = RESTHorizontalAlignment.Center;
    return {
        type: WebChartTypes.Axis,
        visible: true,
        title,
        labels,
        valueFormat: getDefaultNumberFormat(),
        lineSymbol: getDefaultLineSymbol({ color: defaultAxisLineColor }),
    };
}
