/**
 * Returns default axes based on chart type as per the WebChart Specification
 * @param chartType The type of WebChart which is of type WebChartTypes
 *
 */
export async function getDefaultAxes(chartType) {
    const commonStrings = await fetchCommonStrings();
    const defaultAxes = [];
    const xAxisTitle = commonStrings.xAxis;
    const yAxisTitle = commonStrings.statistics.count;
    let xAxis;
    const yAxis = getDefaultValueAxis(yAxisTitle);
    const defaultGridLine = getDefaultLineSymbol({
        opacity: defaultGridLineOpacity,
    });
    yAxis.grid = defaultGridLine;
    switch (chartType) {
        case WebChartTypes.BarSeries: {
            xAxis = getDefaultCategoryAxis(xAxisTitle);
            break;
        }
        case WebChartTypes.ScatterSeries: {
            xAxis = getDefaultValueAxis(xAxisTitle);
            xAxis.grid = defaultGridLine;
            break;
        }
        case WebChartTypes.LineSeries:
        case WebChartTypes.HistogramSeries: {
            xAxis = getDefaultValueAxis(xAxisTitle);
            break;
        }
        case WebChartTypes.BoxPlotSeries: {
            xAxis = getDefaultCategoryAxis("");
            break;
        }
        default:
            break;
    }
    if (xAxis !== undefined) {
        defaultAxes.push(xAxis, yAxis);
    }
    // TODO: once `getDefaultAxes` is only needed via `getDefaultBarChart` deep clone can be removed.
    return defaultAxes;
}
