import { RESTSymbolType, WebChartTypes, WebChartVersionStatus, } from "@arcgis/charts-spec";
import { findMatchingLayerFieldName, getLayerFromQueryObject, } from "../../feature-layer";
import { chartConfigVersions, firstWebMapVersion, unrecognizedVersion, } from "../interfaces";
/**
 * Finds the matching layer fields for a list of fields.
 *
 * @param {(string[] | undefined)} fields
 * @param {SupportedLayer} queryObject
 * @returns
 */
export async function matchFieldsList(fieldsList, queryObject) {
    let fields = fieldsList;
    if (fields !== undefined) {
        fields = await Promise.all(fields.map(async (field) => await findMatchingLayerFieldName(queryObject, field)));
    }
    return fields;
}
/**
 * Adds a iLayer to a WebMapWebChart.
 * If the `queryObject` prop is provided an `iLayer` is added to the config.
 * @param props.chartConfig The chart config with no iLayer
 * @param props.queryObject The layer or feature layer view instance. Its information are **not** used to create the iLayer.
 * @returns The update chart config with a `iLayer` property
 */
export function addILayer(props) {
    const { chartConfig, queryObject } = props;
    let iLayer;
    const layer = getLayerFromQueryObject(queryObject);
    if (layer) {
        // defining the basic mandatory properties for a iLayer
        iLayer = {
            id: layer.id,
            layerType: "ArcGISFeatureLayer",
        };
        if (layer.url) {
            iLayer.url = layer.url;
        }
    }
    return { ...chartConfig, iLayer };
}
// TODO: use other library like the npm semver to handle the validation and comparing of chart versions
/**
 * Compare two version numbers.
 *
 * @param versionA a valid chart version to compare to
 * @param versionB a valid chart version to compare with
 * @returns 1 if versionA > versionB
 *         -1 if versionA < versionB
 *          0 if versionA === versionB
 */
function compareVersions(versionA, versionB) {
    let compareResult = 0; // versionA === versionB
    if (versionA !== versionB) {
        // TODO: check for valid version numbers?
        const partsNumberA = versionA.split(".");
        const partsNumberB = versionB.split(".");
        for (let i = 0; i < partsNumberA.length; i += 1) {
            const valueA = Number.parseInt(partsNumberA[i], 10);
            const valueB = Number.parseInt(partsNumberB[i], 10);
            // if A is larger than B
            if (valueA > valueB || Number.isNaN(valueB)) {
                compareResult = 1;
                break;
            }
            if (valueA < valueB) {
                compareResult = -1;
                break;
            }
        }
    }
    return compareResult;
}
// TODO: use other library like the npm semver to handle the validation and comparing of chart versions
/**
 * Check if a chart version is valid.
 *
 * A version is considered as valid when it is defined, has three components separated by a dot and all are valid numbers.
 * @param chartVersion The version to check.
 * @returns
 */
function isValidChartVersionFormat(chartVersion) {
    const regex = /^([1-9]\d*|0)(\.(([1-9]\d*)|0)){2}$/u;
    return chartVersion !== undefined ? regex.test(chartVersion) : false;
}
/**
 * Retrieve and construct chart version information based on the input version and the currently supported ArcGIS Chart versions
 * defined in `chartConfigVersions`.  Returned information includes:
 *
 * - versionStatus: whether the input chart version is the same | older | newer | unrecognized, comparing with the current chart version,
 * which is defined in `WebChartCurrentVersion`.  An unrecognized version is one that is neither undefined nor conformed to the
 * sematic versioning spec, that is `Major.Minor.Patch`
 * - versions: an array of versions that will be used in the `appConversion()`.
 *  + For any version that is not recognized, versions will be set to `unrecognizedVersion`;
 *  + If the input version is the same or newer, versions will be the input version;
 *  + Otherwise, versions will be a list of versions that is older and closer to the input version, plus all the supported versions
 * that are newer than the input version.
 *
 * Note: The input chart version needs to conform to the format defined by sematic versioning spec; otherwise it will be treated as unrecognized.
 *
 * @param configVersion The input config version.
 */
export function getChartVersionInfo(configVersion) {
    let versionStatus = WebChartVersionStatus.Same;
    let versions = [];
    // TODO: try to resolve user input error? for example 1.0 ==> 1.0.0.  This can be handled in `isValidChartVersion()` if needed
    const validVersionFormat = isValidChartVersionFormat(configVersion);
    if (validVersionFormat && configVersion !== undefined) {
        // filter and sort the `chartConfigVersions` to prevent human errors
        chartConfigVersions.sort(compareVersions);
        // compare the input chart version against the latest one from the list of supported version to see if it's newer
        const compareResult = compareVersions(configVersion, chartConfigVersions[chartConfigVersions.length - 1]);
        if (compareResult === 1) {
            versionStatus = WebChartVersionStatus.Newer;
        }
        else if (compareResult === -1) {
            versionStatus = WebChartVersionStatus.Older;
        }
        // if version is the same or newer...
        if (versionStatus === WebChartVersionStatus.Newer ||
            versionStatus === WebChartVersionStatus.Same) {
            versions.push(configVersion);
        }
        else {
            // if the version is older, try to determine if the version is greater than or equal the second last one in the list
            // the goal is to find the closet version number so that the conversion process will pick it up from there and skip the much older ones.
            for (let i = chartConfigVersions.length - 2; i >= 0; i -= 1) {
                if (compareVersions(configVersion, chartConfigVersions[i]) >= 0) {
                    versions = chartConfigVersions.slice(i, chartConfigVersions.length);
                    break;
                }
            }
            // This should not happen but for some reason, if the version is valid but not found in the supported list,
            // we assign the first web map version so that the conversion process can start from there
            if (versions.length === 0) {
                versions.push(firstWebMapVersion);
            }
        }
    }
    else {
        versionStatus = WebChartVersionStatus.Unrecognized;
        // for now, with invalid (or unrecognized) chart version, we will trigger the conversion to try to convert everything
        versions.push(unrecognizedVersion);
    }
    return { versionStatus, versions };
}
/**
 * Get the series to use as a reference for the chart. That's the one containing important common information like the query,
 * the split-by field, etc.
 * For box plot we need to handle the specific case of the mean line series.
 * @param chartConfig The chart config
 */
export function getSeriesReference(chartConfig) {
    const { series: seriesConfig } = chartConfig;
    const showMean = "showMean" in chartConfig ? chartConfig.showMean : false;
    // in mean lines mode for box plot (showMean: true), the first series doesn't always contain the expected information.
    // In that case we look for the first line series instead
    let seriesReference = showMean === true
        ? seriesConfig.find((s) => s.type === WebChartTypes.LineSeries)
        : seriesConfig[0];
    seriesReference ?? (seriesReference = seriesConfig[0]);
    return seriesReference;
}
/**
 * Find the expected symbol type for a guide given the start and end values.
 * @param guide The guide to analyze
 */
export function findExpectedGuideSymbol(start, end) {
    const startValue = start !== null ? start.toString() : null;
    const endValue = end !== null && end !== undefined ? end.toString() : null;
    return endValue !== null && endValue !== startValue
        ? RESTSymbolType.SFS
        : RESTSymbolType.SLS;
}
