import { WebChartTypes } from "@arcgis/charts-spec";
import { getChartOrSeriesType } from "../misc";
export function getChartIcon(chart) {
    const chartType = getChartOrSeriesType(chart.series);
    let chartIcon = "graph-bar";
    switch (chartType) {
        case WebChartTypes.LineSeries: {
            chartIcon = "graph-time-series";
            break;
        }
        case WebChartTypes.PieSeries: {
            chartIcon = "pie-chart";
            break;
        }
        case WebChartTypes.HistogramSeries: {
            chartIcon = "graph-histogram";
            break;
        }
        case WebChartTypes.ScatterSeries: {
            chartIcon = "graph-scatter-plot";
            break;
        }
        case WebChartTypes.BoxPlotSeries: {
            chartIcon = "box-chart";
            break;
        }
        case WebChartTypes.BarSeries:
        default: {
            break;
        }
    }
    return chartIcon;
}
