import { RESTUnits } from "@arcgis/charts-spec";
import { importCorePromiseUtils, importRequest } from "@arcgis/core-adapter";
import { mapQuery, getMaxRecordCountFactor, getMaxRecords, combineFilters, mapQueryTimeExtent, mapQueryGeometry, isQueryCacheable, } from "../query-parser";
import { doesLayerSupportQueryWithDistance, getLayerFromQueryObject, getMainLayer } from "../feature-layer";
import { abortControllerMap } from "./interfaces";
import { queryDefaults } from "../defaults";
import { ErrorKeywords } from "../errors/interfaces";
import { ArcgisChartsError } from "../errors";
import { fetchErrorsStrings } from "../t9n/errors";
import { ChartSubType } from "../misc/interfaces";
/**
 * Converts a RESTUnits into a value that can directly be used by the query analytic.
 * @param units the RESTUnits property to convert
 */
function convertSpatialFilterUnits(units) {
    let convertedUnit;
    switch (units) {
        case RESTUnits.Feet:
            convertedUnit = "esriSRUnit_Foot";
            break;
        case RESTUnits.Miles:
            convertedUnit = "esriSRUnit_StatuteMile";
            break;
        case RESTUnits.NauticalMiles:
            convertedUnit = "esriSRUnit_NauticalMile";
            break;
        case RESTUnits.USNauticalMiles:
            convertedUnit = "esriSRUnit_USNauticalMile";
            break;
        case RESTUnits.Meters:
            convertedUnit = "esriSRUnit_Meter";
            break;
        case RESTUnits.Kilometers:
            convertedUnit = "esriSRUnit_Kilometer";
            break;
        default:
            break;
    }
    return convertedUnit;
}
/**
 * Performs a query analytic.
 * @param props.queryObject The object used to query (FL or FLV)
 * @param props.queryConfig The WebChartQuery
 * @param props.outAnalytics The out analytics
 * @param props.outputFormat The output format
 * @param props.dataFormat The data format
 */
export async function queryAnalytic(props) {
    const { queryObject, queryConfig, outputFormat, dataFormat, outAnalytics } = props;
    const { where: filter, geometry, distance, units } = queryConfig;
    let analyticData;
    const layer = getLayerFromQueryObject(queryObject);
    const mainLayer = getMainLayer(layer);
    // building the filter
    const where = combineFilters([filter, mainLayer.definitionExpression]);
    // adding an option to the query analytic to apply the filters
    const options = { query: { where } };
    // Adding the extent
    const geometryExtent = (await mapQueryGeometry(geometry))?.extent?.toJSON();
    if (geometryExtent !== undefined) {
        const convertedUnit = convertSpatialFilterUnits(units);
        const { xmin, ymin, xmax, ymax } = geometryExtent;
        options.query = {
            ...options.query,
            geometryType: "esriGeometryEnvelope",
            geometry: `${xmin},${ymin},${xmax},${ymax}`,
            distance,
            units: convertedUnit,
            inSR: geometry?.spatialReference?.wkid,
        };
    }
    const queryURL = `${mainLayer.url}/${mainLayer.layerId}/queryAnalytic?f=${outputFormat}&dataFormat=${dataFormat}&outAnalytics=${outAnalytics}`;
    try {
        const esriRequest = await importRequest();
        // Performing the query and fetching the results
        const results = await esriRequest(queryURL, options);
        analyticData = results.data?.features?.[0]?.attributes;
    }
    catch (e) {
        const errorStrings = await fetchErrorsStrings();
        throw new ArcgisChartsError(ErrorKeywords.QueryFeaturesError, errorStrings.queryError, e);
    }
    return analyticData;
}
/**
 * Performs a query feature count based on the provided parameters.
 * @param props.queryObject The object used to query (FL or FLV)
 * @param props.queryConfig The WebChartQuery containing parameters about the query to execute
 * @param props.chartSubType The chart sub type
 * @param props.x The x field
 * @param props.isGaugeByFeature Indicate whether it's a gauge by feature. Will be defaulted to `false` if not provided.
 */
export async function queryFeatureCount(props) {
    const { queryConfig, x, queryObject, chartSubType, isGaugeByFeature } = props;
    const layer = getLayerFromQueryObject(queryObject);
    let categoryOrDateCount;
    if (queryObject !== undefined) {
        // Building query parameters in order to perform a count distinct on the object ID when there's no aggregation and on the category or date field otherwise
        const fieldOID = layer.objectIdField;
        const { where: filter, gdbVersion, timeExtent, geometry, distance, units } = queryConfig;
        const queryTimeExtent = await mapQueryTimeExtent(timeExtent);
        const queryGeometry = await mapQueryGeometry(geometry);
        const mainLayer = getMainLayer(layer);
        const where = combineFilters([filter, mainLayer.definitionExpression]);
        const queryParams = {
            returnDistinctValues: true,
            // See issue #2653. We need a unique value for the outFields, no matter what it is, except "*", "" or multiple values.
            outFields: isGaugeByFeature === true ||
                chartSubType === ChartSubType.PieNoAggregation ||
                chartSubType === ChartSubType.BarAndLineNoAggregation ||
                chartSubType === ChartSubType.BarAndLineSplitByNoAggregation
                ? [fieldOID]
                : [x],
            where,
            gdbVersion: gdbVersion ?? mainLayer.gdbVersion,
            timeExtent: queryTimeExtent ?? mainLayer.timeExtent,
            geometry: queryGeometry,
        };
        // we set `cacheHint` to true only when the query can be cached
        if (isQueryCacheable(queryParams)) {
            queryParams.cacheHint = true;
        }
        if (queryParams.geometry !== undefined && doesLayerSupportQueryWithDistance(layer)) {
            // adding the buffer (distance + units) when the geometry is provided and the layer is compatible
            if (distance !== undefined) {
                queryParams.distance = distance;
            }
            if (units !== undefined) {
                queryParams.units = units;
            }
        }
        try {
            categoryOrDateCount = await queryObject.queryFeatureCount(queryParams);
        }
        catch (e) {
            // When this query counting features fails, we still want to continue and fetch the data.
            // The results will be counted afterwards to avoid rendering the chart if there are too many elements.
            categoryOrDateCount = Number.NaN;
        }
    }
    return categoryOrDateCount;
}
/**
 * Fetches multiple statistics (min, max, count...).
 * Returns an object mapping the outStatisticFieldName with its value
 * @param props
 */
export async function queryStatistics(props) {
    const { queryObject, queryConfig, abortKey } = props;
    const { outStatistics } = queryConfig;
    const statsResult = new Map();
    if (queryObject !== undefined && outStatistics !== undefined && outStatistics.length > 0) {
        const modifiedOutStatistics = outStatistics.map((stat) => {
            const { statisticType, onStatisticField, outStatisticFieldName } = stat;
            return {
                statisticType,
                onStatisticField,
                outStatisticFieldName: outStatisticFieldName?.toUpperCase(),
            };
        });
        queryConfig.outStatistics = modifiedOutStatistics;
        // no need to limit the number of features on a query stat (it would also fail the query)
        delete queryConfig.num;
        // no need to order the results (it would also fail the query)
        delete queryConfig.orderByFields;
        const query = await mapQuery({
            queryObject,
            queryConfig,
        });
        let features;
        try {
            features = await queryObject.queryFeatures(query, {
                signal: abortKey !== undefined ? abortControllerMap.get(abortKey)?.signal : undefined,
            });
        }
        catch (e) {
            const promiseUtils = await importCorePromiseUtils();
            // Not throwing errors when we're aborting previous queries
            if (!promiseUtils.isAbortError(e)) {
                const errorStrings = await fetchErrorsStrings();
                throw new ArcgisChartsError(ErrorKeywords.QueryFeaturesError, errorStrings.queryError, e);
            }
        }
        const queryResult = features?.features?.[0]?.attributes;
        if (queryResult !== undefined) {
            // storing the results into the map
            outStatistics.forEach((stat) => {
                const { outStatisticFieldName } = stat;
                if (outStatisticFieldName !== undefined) {
                    // looking at different cases in the result at some servers do that transformation
                    const stat = queryResult[outStatisticFieldName.toLowerCase()] ??
                        queryResult[outStatisticFieldName.toUpperCase()] ??
                        queryResult[outStatisticFieldName];
                    if (stat !== undefined) {
                        statsResult.set(outStatisticFieldName, stat);
                    }
                }
            });
        }
    }
    return statsResult;
}
async function queryFeatures(props) {
    const { queryObject, queryConfig, returnUniqueIdsOnly, abortKey } = props;
    let graphics = [];
    const layer = getLayerFromQueryObject(queryObject);
    if (queryObject !== undefined) {
        const maxRecordCountFactor = getMaxRecordCountFactor(layer);
        const query = await mapQuery({
            queryObject,
            queryConfig,
            maxRecordCountFactor,
        });
        try {
            if (returnUniqueIdsOnly === true) {
                if ("queryObjectIds" in queryObject) {
                    graphics = await queryObject.queryObjectIds(query);
                }
                else {
                    // `queryObjectIds` is not available yet on `SubtypeSubLayer`, hence this workaround
                    // Refer to https://devtopia.esri.com/WebGIS/arcgis-charts/issues/8289
                    // TODO: in the version 4.31 of @arcgis/core `queryObjectIds` will be available on SubtypeSubLayer and when it happens we can remove this workaround
                    // keeping only the object id
                    query.outFields = [layer.objectIdField];
                    const queryResult = await queryObject.queryFeatures(query, {
                        signal: abortKey !== undefined ? abortControllerMap.get(abortKey)?.signal : undefined,
                    });
                    // mapping the results to keep the object id only
                    graphics = queryResult.features.map((f) => f.attributes[layer.objectIdField]);
                }
            }
            else {
                const seriesData = await queryObject.queryFeatures(query, {
                    signal: abortKey !== undefined ? abortControllerMap.get(abortKey)?.signal : undefined,
                });
                graphics = seriesData.features;
            }
        }
        catch (e) {
            const promiseUtils = await importCorePromiseUtils();
            // Not throwing errors when we're aborting previous queries
            if (!promiseUtils.isAbortError(e)) {
                const errorStrings = await fetchErrorsStrings();
                throw new ArcgisChartsError(ErrorKeywords.QueryFeaturesError, errorStrings.queryError, e);
            }
        }
    }
    return graphics;
}
async function batchQueryRequests(queryProps) {
    const pages = [];
    const { featureCount, maxRecords, queryConfig } = queryProps;
    let rawBatchData = [];
    let i = 0;
    let start = queryConfig?.start ?? 0;
    while (i < queryDefaults.maxParallelRequests && start < featureCount) {
        const pageProps = { ...queryProps };
        // we adjust precisely the number of features to fetch. For instance if we fetch 10,000 per batch,
        // while featureCount is set to 11,223, the `num` will be set to 10,000 on the first round and 1,223 on the second.
        pageProps.queryConfig = {
            ...queryConfig,
            start,
            num: Math.min(maxRecords, featureCount - start),
        };
        pages.push(queryFeatures(pageProps));
        start += maxRecords;
        i += 1;
    }
    const pAll = await Promise.all(pages);
    rawBatchData = pAll.flat();
    return rawBatchData;
}
/**
 * Fetches all the features from the input QueryObject, according to the other specified input settings (query, outFields...).
 * Handles the paging.
 * @param props
 */
export async function queryAllFeatures(props) {
    const { queryObject, queryConfig, returnUniqueIdsOnly, featureCount, noBatch, abortKey } = props;
    let rawData = [];
    const layer = getLayerFromQueryObject(queryObject);
    if (queryObject !== undefined) {
        // If the number of features was not previously computed we default it to 1 to get results without triggering the pagination
        const numberOfFeatures = featureCount ?? 1;
        const maxRecords = getMaxRecords(layer);
        if (noBatch !== true && numberOfFeatures > maxRecords && layer.capabilities.query.supportsPagination) {
            // Do Paging, send maxRecords per paged query until we get all records
            let batchStart = 0;
            let i = 0;
            const totalPages = Math.ceil(numberOfFeatures / maxRecords);
            const totalBatches = Math.ceil(totalPages / queryDefaults.maxParallelRequests);
            const batchResults = [];
            while (i < totalBatches) {
                batchStart = i * queryDefaults.maxParallelRequests * maxRecords;
                const perBatchRawData = batchQueryRequests({
                    queryConfig: { ...queryConfig, start: batchStart },
                    queryObject,
                    maxRecords,
                    featureCount: numberOfFeatures,
                    returnUniqueIdsOnly,
                    abortKey,
                });
                batchResults.push(perBatchRawData);
                i += 1;
            }
            const batchResultsData = await Promise.all(batchResults);
            rawData = batchResultsData.flat();
        }
        else if (Number.isNaN(numberOfFeatures) || numberOfFeatures >= 0) {
            // We continue even if `numberOfFeatures` is 0. This is due to the query counting the features that can ignore special data like `null`.
            // Refer to this issue for use case:https://devtopia.esri.com/WebGIS/arcgis-charts/issues/6610
            rawData = await queryFeatures({
                queryObject,
                queryConfig,
                returnUniqueIdsOnly,
                abortKey,
            });
        }
    }
    return rawData;
}
/**
 * Fetches the extent of all the features that are represented by their object ids
 * @param props
 */
export async function queryUniqueIDs(props) {
    const { queryObject, queryConfig, uniqueFeature, abortKey } = props;
    const modifiedQueryConfig = { ...queryConfig, returnDistinctValues: true };
    // Executing the series of queries to get unique Ids from the field
    const allFeatures = (await queryAllFeatures({
        queryObject,
        abortKey,
        queryConfig: modifiedQueryConfig,
        returnUniqueIdsOnly: true,
        featureCount: uniqueFeature === true ? 1 : undefined, // in case of querying for a unique feature, we know the query will bring back only 1 entry
    }));
    return allFeatures;
}
/**
 * Aborts all existing queries and initializes a controller for new ones. The queries are associated
 * to a unique key to avoid cancelling queries for other charts.
 * @param key The key pointing to the abort controller to execute
 */
export function abortPendingQueries(key) {
    if (key !== undefined) {
        // Looking for an existing controller
        const chartAbortController = abortControllerMap.get(key);
        // If it exists, we call an abort on it
        if (chartAbortController !== undefined) {
            chartAbortController.abort();
        }
        // Setting a new controller for the next round of queries
        abortControllerMap.set(key, new AbortController());
    }
}
