import { WebChartTypes, WebChartDataTransformations, WebChartTimeIntervalUnits, OverlayTerms, WebChartSortOrderKinds, } from "@arcgis/charts-spec";
import Sanitizer from "@esri/arcgis-html-sanitizer";
import { getChartLimits, getDefaultChartRotation, getDefaultStackedType, queryCalculatedExpr } from "../defaults";
import { LimitBehavior } from "../defaults/interfaces";
import { ChartSubType } from "./interfaces";
/**
 * Returns the color policy for the chart.  If it's not defined, `WebChartColoringPatterns.Single` will be used.
 * @param chartConfig the chart config.
 * @returns the color policy.
 */
export function getColorMatch(chartConfig) {
    return chartConfig?.colorMatch ?? false;
}
/**
 * Determines if a Set of series type matches a combo bar-line chart
 * @param uniqueTypes The set of unique series types
 */
function isComboLineAndBarChart(uniqueTypes) {
    return (uniqueTypes.size === 2 && uniqueTypes.has(WebChartTypes.LineSeries) && uniqueTypes.has(WebChartTypes.BarSeries));
}
/**
 * Determines if a Set of series type matches a box plot
 * @param uniqueTypes The set of unique series types
 */
function isBoxPlotChart(uniqueTypes) {
    return ((uniqueTypes.size === 2 &&
        uniqueTypes.has(WebChartTypes.LineSeries) &&
        uniqueTypes.has(WebChartTypes.BoxPlotSeries)) ||
        (uniqueTypes.size === 1 && uniqueTypes.has(WebChartTypes.BoxPlotSeries)));
}
/**
 * Getting the type of a chart or series. If the input parameters are incorrect (empty array of series, or a mix of unknown/unallowed series types) the function returns `undefined`.
 * To get the series type, a valid seriesIndex is required. Otherwise, the type of a chart is determined and returned.
 * @param chartSeries The array of chart's series
 * @param seriesIndex An index to get a specific series' type.  A valid index is required if the goal is to get the type of the series.
 */
export function getChartOrSeriesType(chartSeries, seriesIndex) {
    let seriesType;
    if (chartSeries !== undefined && chartSeries.length > 0) {
        // get the series type for a specific series based on the index
        if (seriesIndex !== undefined && seriesIndex >= 0 && seriesIndex < chartSeries.length) {
            seriesType = chartSeries[seriesIndex].type;
        }
        else {
            const uniqueTypes = new Set(chartSeries.map((s) => s.type));
            const isBarLineCombo = isComboLineAndBarChart(uniqueTypes);
            const isBoxPlot = isBoxPlotChart(uniqueTypes);
            if (isBarLineCombo === true) {
                seriesType = WebChartTypes.ComboLineAndBarSeries;
            }
            else if (isBoxPlot) {
                seriesType = WebChartTypes.BoxPlotSeries;
            }
            else if (uniqueTypes.size >= 2) {
                // if the series array still has multiple types of series, we return `undefined`
                seriesType = undefined;
            }
            else {
                seriesType = chartSeries[0].type;
            }
        }
    }
    return seriesType;
}
/**
 * Indicates whether or not the chart is rotated.
 * @param chartConfig the chart config.
 * @returns
 */
export function isChartRotated(chartConfig) {
    let isRotated = getDefaultChartRotation();
    if (chartConfig !== undefined) {
        const chartType = getChartOrSeriesType(chartConfig.series);
        if (chartType === WebChartTypes.BarSeries ||
            chartType === WebChartTypes.LineSeries ||
            chartType === WebChartTypes.ComboLineAndBarSeries ||
            chartType === WebChartTypes.BoxPlotSeries) {
            // check the rotation property on the chart, then the chartDefaults if `rotated` property is not available, in that order.
            isRotated = chartConfig.rotated ?? getDefaultChartRotation();
        }
    }
    return isRotated;
}
/**
 * Getting the list of series types from a series config array.
 * @param chartSeries The array of chart's series
 */
export function getSeriesTypes(chartSeries) {
    return chartSeries?.map((series) => series.type) ?? [];
}
/**
 * Returns the type of stacking (side-by-side, stacked...) to be used to display the chart.
 * For Line, Bar, Box-plot, and Radar chart, default option is 'sideBySide'
 * @param series
 */
export function getStackedType(config) {
    let stackedType = config?.stackedType;
    if (stackedType === undefined && config !== undefined) {
        const chartType = getChartOrSeriesType(config.series);
        if (chartType === WebChartTypes.BarSeries ||
            chartType === WebChartTypes.LineSeries ||
            chartType === WebChartTypes.ComboLineAndBarSeries ||
            chartType === WebChartTypes.RadarSeries) {
            stackedType = getDefaultStackedType();
        }
    }
    return stackedType;
}
/**
 * Escapes a string which has special characters.
 * This function is adapted from an [MDN example](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Guide/Regular_Expressions).
 * currently added only the needed special characters ',", \, / that are to be escaped based on discussion with ArcGIS Online Feature Service team.
 * Single quote is handled differently instead of escaping it with backslash it is escaped with single quote. example: Cote d'Ivoire -> Cote d''Ivoire
 * @param string input string to replace for any special characters.
 */
function escapeString(string) {
    let escapedString = string;
    escapedString = escapedString.replace(/'/gu, "''");
    escapedString = escapedString.replace(/[/\\"|[\]\\]/gu, "\\$&");
    return escapedString;
}
/**
 * Checks if the input string value contains any non-latin character(s).
 * @param value
 */
function containsNonLatinCharacter(value) {
    let hasNonLatinChars = false;
    if (typeof value !== "number") {
        const chars = [...value];
        hasNonLatinChars = chars.some((char) => char.charCodeAt(0) > 255);
    }
    return hasNonLatinChars;
}
/**
 * Escapes any single quote in the input string with a single quote for SQL.
 * For example: Cote d'Ivoire -> Cote d''Ivoire
 * @param string input string to replace for any special characters.
 */
export function escapeSQLString(string) {
    let escapedString = string;
    let additionalQuotes = "";
    if (string.startsWith("'") && string.endsWith("'")) {
        escapedString = string.slice(1, -1);
        // Store this so we will add back the surrounding quotes to the value
        additionalQuotes = "'";
    }
    escapedString = escapedString.replace(/'/gu, "''");
    // Prefix 'N' to the returned string if it contain non-latin character(s).
    let prefix = "";
    if (containsNonLatinCharacter(escapedString)) {
        prefix = "N";
    }
    return `${prefix}${additionalQuotes}${escapedString}${additionalQuotes}`;
}
/**
 * Normalizes a string by
 *   - trimming off white spaces
 *   - if the value is a string and keepSurroundingQuotes is not true: stripping ' (single inverted comma), if present on both sides
 *   - if escape is true (or undefined): escaping special characters like ", \, /.
 * E.g.:
 * "'alpha'" => "alpha"
 * "'alpha" => No change
 * "alpha'" => No change
 * "alp'ha'" => alp''ha
 * "  'alpha'" => "alpha"
 * "alp"\/ha" => "alp\"\\\/ha"
 * @param props.value The string to normalize
 * @param props.keepSurroundingQuotes Whether to keep the surrounding quotes; default is false
 * @param props.escape Whether to escape special characters; default is true
 */
export function normalizeString(props) {
    const { value, keepSurroundingQuotes, escape } = props;
    let normalizedValue = value?.trim();
    if (!isEmptyStringField(normalizedValue)) {
        let additionalQuotes = "";
        if (normalizedValue.startsWith("'") && normalizedValue.endsWith("'")) {
            normalizedValue = normalizedValue.slice(1, -1);
            // if keepSurroundingQuotes is true, we will add back the surrounding quotes to the value
            if (keepSurroundingQuotes) {
                additionalQuotes = "'";
            }
        }
        // escaping the special characters if boolean is true or undefined
        if (escape !== false) {
            normalizedValue = escapeString(normalizedValue);
        }
        // Adding back the potential quotes. if the value was a number `additionalQuotes` will be empty
        normalizedValue = `${additionalQuotes}${normalizedValue}${additionalQuotes}`;
    }
    return normalizedValue;
}
// TODO: CT: what is the limit for combo chart? For now use the same limit for bar chart
/**
 * Indicates whether a bar, line or pie chart respects the number limit of elements (bars, markers or slices).
 * For bar chart, different limits are set according to the number of series.
 * For line chart, a limit on the number of series and the total number of markers are set.
 * For pie chart, a limit on the number of slices is set.
 * @param props.count Number total of elements on the chart
 * @param props.seriesCount Number of series on the chart
 * @param props.seriesType The series type
 * @param props.chartLimits The runtime chart limits
 */
export function isNumberOfElementsUnderLimit(props) {
    const { count, seriesCount, chartType, chartLimits } = props;
    const { behaviorAfterLimit } = chartLimits ?? {};
    let limitNotExceeded = true;
    // if the expected behavior is to render up to the limit, we consider the limit as never exceeded
    if (behaviorAfterLimit !== LimitBehavior.RenderUpToTheLimit) {
        const { maxBarChartSeriesCount, maxBarUniqueSeriesCountTotal, maxBarTwoSeriesCountPerSeries, maxBarTwoSeriesCountTotal, maxBarThreePlusSeriesCountPerSeries, maxBarThreePlusSeriesCountTotal, maxLineChartSeriesCount, maxLineUniqueSeriesCountTotal, maxLineTwoSeriesCountPerSeries, maxLineTwoSeriesCountTotal, maxLineThreePlusSeriesCountPerSeries, maxLineThreePlusSeriesCountTotal, maxPieChartSliceCountTotal, maxGaugeByFeaturesLimit, maxBoxPlotSeriesCount, maxBoxUniqueSeriesCountTotal, maxBoxTwoSeriesCountTotal, maxBoxTwoSeriesCountPerSeries, maxBoxThreePlusSeriesCountTotal, maxBoxThreePlusSeriesCountPerSeries, } = getChartLimits(chartLimits);
        switch (chartType) {
            case WebChartTypes.BarSeries:
            case WebChartTypes.ComboLineAndBarSeries:
                {
                    const categoryCount = count / seriesCount;
                    if (seriesCount <= maxBarChartSeriesCount) {
                        if (seriesCount === 0) {
                            limitNotExceeded = true;
                        }
                        else if (seriesCount === 1) {
                            limitNotExceeded = count <= maxBarUniqueSeriesCountTotal;
                        }
                        else if (seriesCount === 2) {
                            limitNotExceeded = count <= maxBarTwoSeriesCountTotal && categoryCount <= maxBarTwoSeriesCountPerSeries;
                        }
                        else if (seriesCount > 2) {
                            limitNotExceeded =
                                count <= maxBarThreePlusSeriesCountTotal && categoryCount <= maxBarThreePlusSeriesCountPerSeries;
                        }
                    }
                    else {
                        limitNotExceeded = false;
                    }
                }
                break;
            case WebChartTypes.LineSeries:
            case WebChartTypes.RadarSeries:
                {
                    const categoryCount = count / seriesCount;
                    if (seriesCount <= maxLineChartSeriesCount) {
                        if (seriesCount === 0) {
                            limitNotExceeded = true;
                        }
                        else if (seriesCount === 1) {
                            limitNotExceeded = count <= maxLineUniqueSeriesCountTotal;
                        }
                        else if (seriesCount === 2) {
                            limitNotExceeded = count <= maxLineTwoSeriesCountTotal && categoryCount <= maxLineTwoSeriesCountPerSeries;
                        }
                        else if (seriesCount > 2) {
                            limitNotExceeded =
                                count <= maxLineThreePlusSeriesCountTotal && categoryCount <= maxLineThreePlusSeriesCountPerSeries;
                        }
                    }
                    else {
                        limitNotExceeded = false;
                    }
                }
                break;
            case WebChartTypes.PieSeries:
                limitNotExceeded = count <= maxPieChartSliceCountTotal;
                break;
            case WebChartTypes.BoxPlotSeries:
                {
                    const categoryCount = count / seriesCount;
                    if (seriesCount <= maxBoxPlotSeriesCount) {
                        if (seriesCount === 0) {
                            limitNotExceeded = true;
                        }
                        else if (seriesCount === 1) {
                            limitNotExceeded = count <= maxBoxUniqueSeriesCountTotal;
                        }
                        else if (seriesCount === 2) {
                            limitNotExceeded = count <= maxBoxTwoSeriesCountTotal && categoryCount <= maxBoxTwoSeriesCountPerSeries;
                        }
                        else if (seriesCount > 2) {
                            limitNotExceeded =
                                count <= maxBoxThreePlusSeriesCountTotal && categoryCount <= maxBoxThreePlusSeriesCountPerSeries;
                        }
                    }
                    else {
                        limitNotExceeded = false;
                    }
                }
                break;
            case WebChartTypes.GaugeSeries:
                // only applied to FeatureGauge
                limitNotExceeded = count <= maxGaugeByFeaturesLimit;
                break;
            default:
                break;
        }
    }
    return limitNotExceeded;
}
/**
 * Returns the alias for the field name if it exists; otherwise return the field name itself.
 * @param fieldName The field name to look for in the field array
 * @param fields The list of fields containing all the information regarding the fields, including the alias
 */
export function getFieldAlias(fields = [], fieldName) {
    const fieldAlias = fields.find((field) => field.name === fieldName)?.alias;
    return fieldAlias !== "" ? fieldAlias ?? fieldName : fieldName;
}
/**
 * Return the field name for the field alias if it exists; otherwise return the field alias itself.
 * @param fieldAlias The field alias to look for in the field array
 * @param fields The list of fields containing all the information regarding the fields, including the alias
 */
export function getFieldNameFromFieldAlias(fields = [], fieldAlias) {
    const fieldName = fields.find((field) => field.alias === fieldAlias)?.name;
    return fieldName !== "" ? fieldName ?? fieldAlias : fieldAlias;
}
/**
 * Decides whether we can show the legend or not. If it returns `false`, then we should not show the legend even if `legend.visible` is `true`.
 * For Pie charts, hide the legend when there is only one slice.
 * @param chartSeries
 */
export function canShowLegend(chartSeries) {
    let canShow = false;
    const chartType = getChartOrSeriesType(chartSeries);
    switch (chartType) {
        case WebChartTypes.PieSeries:
            canShow = true;
            break;
        case WebChartTypes.BarSeries:
        case WebChartTypes.LineSeries:
        case WebChartTypes.ComboLineAndBarSeries:
        case WebChartTypes.BoxPlotSeries:
            canShow = chartSeries.length > 1;
            break;
        case WebChartTypes.HistogramSeries:
        case WebChartTypes.ScatterSeries: {
            const overlayBasedSeries = chartSeries[0];
            canShow =
                overlayBasedSeries.overlays !== undefined &&
                    Object.values(overlayBasedSeries.overlays).some((overlay) => overlay.created === true);
            break;
        }
        default:
            break;
    }
    return canShow;
}
/**
 * Returns whether a scatter plot is currently supported
 * @param config The chart's config
 */
function isSupportedScatterPlot(config) {
    let supported = true;
    const chartType = getChartOrSeriesType(config.series);
    if (chartType === WebChartTypes.ScatterSeries) {
        // excluding scatter plot with multiple series (see issue #6001)
        supported = config.series.length === 1;
    }
    return supported;
}
/**
 * Returns whether a histogram is currently supported
 * @param config The chart's config
 */
function isSupportedHistogram(config) {
    let supported = true;
    const chartType = getChartOrSeriesType(config.series);
    if (chartType === WebChartTypes.HistogramSeries) {
        // Histogram has new transformation types not supported at v1 Charts (see issue #2748 )
        supported =
            config.series.length <= 1 &&
                config.series[0].type === WebChartTypes.HistogramSeries &&
                (!("dataTransformationType" in config.series[0]) ||
                    (config.series[0].dataTransformationType !== undefined &&
                        Object.values(WebChartDataTransformations).includes(config.series[0].dataTransformationType)));
    }
    return supported;
}
/**
 * Returns whether a pie chart is currently supported
 * @param config The chart's config
 */
function isSupportedPieChart(config) {
    let supported = true;
    const chartType = getChartOrSeriesType(config.series);
    if (chartType === WebChartTypes.PieSeries) {
        supported = config.series.length <= 1;
    }
    return supported;
}
/**
 * Based on a provided list of supported chart types, this function returns a boolean indicating if the config
 * contains supported series. An extra test is done for the scatter plot, pie, and histogram series.
 * @param supportedChartTypes The list of supported chart types
 * @param config The chart's config
 */
export function isSupportedChart(supportedChartTypes, config) {
    const chartType = getChartOrSeriesType(config?.series);
    // global test: checking if the chart type in included within the list of supported types
    let supported = supportedChartTypes.includes(chartType);
    // additional test for specific chart types
    if (supported && config !== undefined) {
        switch (chartType) {
            case WebChartTypes.HistogramSeries:
                supported = isSupportedHistogram(config);
                break;
            case WebChartTypes.ScatterSeries:
                supported = isSupportedScatterPlot(config);
                break;
            case WebChartTypes.PieSeries:
                supported = isSupportedPieChart(config);
                break;
            default:
                break;
        }
    }
    return supported;
}
/**
 * Indicates whether a string field is empty (different from undefined, null and empty string).
 * The value is stringified before being trimmed to debunk edge cases like when the value is a numeric value.
 * @param value The value to test
 * @returns `true` if empty, `false` otherwise
 */
export function isEmptyStringField(value) {
    return value === undefined || value === null || (typeof value === "string" && value.trim() === "");
}
/**
 * Indicates whether an object contains a property key from list of properties.
 * @param data the object to look into
 * @param propertyList the array of property keys to search the data if one of them is present.
 * @returns An object containing a boolean indicating the propertyList presence, and the property key that matched
 */
export function isPropertyKeyInData(data, propertyList) {
    const expr = propertyList.find((testedExpr) => testedExpr in data);
    const isInItem = expr !== undefined;
    return { isInItem, expr };
}
/**
 * Indicates whether an object contains the result of a calculated expression coming from a
 * query (similar to "EXPR_1").
 * @param dataItem the object to look into
 * @returns An object containing a boolean indicating the expression's presence, and the expression key that matched
 */
export function isQueryCalculatedExprInItem(dataItem) {
    // queryCalculatedExpr is an array containing the possible keys (EXPR_1, expr1...)
    return isPropertyKeyInData(dataItem, queryCalculatedExpr);
}
/**
 * Finds localized string for the given `WebChartTimeIntervalUnits` unit type.
 * @param unit
 * @param strings
 * @returns Localized unit type label
 */
export function getTimeIntervalUnitLabel(unit, strings) {
    let unitLabel;
    switch (unit) {
        case WebChartTimeIntervalUnits.Seconds:
            unitLabel = strings.seconds;
            break;
        case WebChartTimeIntervalUnits.Minutes:
            unitLabel = strings.minutes;
            break;
        case WebChartTimeIntervalUnits.Hours:
            unitLabel = strings.hours;
            break;
        case WebChartTimeIntervalUnits.Days:
            unitLabel = strings.days;
            break;
        case WebChartTimeIntervalUnits.Weeks:
            unitLabel = strings.weeks;
            break;
        case WebChartTimeIntervalUnits.Months:
            unitLabel = strings.months;
            break;
        case WebChartTimeIntervalUnits.Years:
            unitLabel = strings.years;
            break;
        default:
            unitLabel = "";
            break;
    }
    return unitLabel;
}
/**
 * Returns the appropriate `index` to fetch the default color from `overlayRamp[]` array for a histogram overlay
 * @param overlayType
 */
export function getOverlayColorIndex(overlayType) {
    let overlayIndex;
    switch (overlayType) {
        case OverlayTerms.Mean:
            overlayIndex = 1;
            break;
        case OverlayTerms.Median:
            overlayIndex = 2;
            break;
        case OverlayTerms.StdDev:
            overlayIndex = 3;
            break;
        case OverlayTerms.Normal:
        default:
            overlayIndex = 0;
            break;
    }
    return overlayIndex;
}
/**
 * Assigns visible property to a series or an overlay
 * @param props.config The config that gets updated for each series visibility
 * @param props.event The event that is currently being called
 */
export function updateSeriesVisibility(event, config) {
    if (config !== undefined) {
        const { seriesId, overlayType, visible } = event.detail;
        let toggledSeries;
        switch (overlayType) {
            case OverlayTerms.Mean:
                if (config.series[0].type === WebChartTypes.HistogramSeries) {
                    toggledSeries = config.series[0].overlays?.mean;
                }
                break;
            case OverlayTerms.Median:
                if (config.series[0].type === WebChartTypes.HistogramSeries) {
                    toggledSeries = config.series[0].overlays?.median;
                }
                break;
            case OverlayTerms.StdDev:
                if (config.series[0].type === WebChartTypes.HistogramSeries) {
                    toggledSeries = config.series[0].overlays?.standardDeviation;
                }
                break;
            case OverlayTerms.Normal:
                if (config.series[0].type === WebChartTypes.HistogramSeries) {
                    toggledSeries = config.series[0].overlays?.comparisonDistribution;
                }
                break;
            case OverlayTerms.Trendline:
                if (config.series[0].type === WebChartTypes.ScatterSeries) {
                    toggledSeries = config.series[0].overlays?.trendLine;
                }
                break;
            default:
                toggledSeries = config.series.find((series) => series.id === seriesId);
                break;
        }
        // Apply series toggle
        if (toggledSeries !== undefined) {
            toggledSeries.visible = visible;
        }
    }
}
/**
 * Sanitize an HTML string to prevent XSS attacks (https://owasp.org/www-community/attacks/xss/)
 * @param inputToSanitize The HTML string to sanitize
 */
export function sanitizeHTMLOutput(inputToSanitize) {
    const sanitizer = new Sanitizer();
    return sanitizer.sanitize(inputToSanitize);
}
/** .
 * Indicates if a provided number value is valid.
 * An invalid value is: undefined | NaN | Infinite | -Infinite | null
 * See for more details:
 *   - https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/isFinite
 *   - https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/isNaN
 */
export function isValidNumber(value) {
    return value !== undefined && value !== null && !Number.isNaN(value) && Number.isFinite(value);
}
/**
 * Updates the chart rotation for a given WebChart config.
 * Returns the updated config.
 * @param newRotation new rotation
 * @param config the chart config
 */
export function updateRotation(newRotation, config) {
    let updatedConfig;
    if (config !== undefined) {
        updatedConfig = { ...config, rotated: newRotation };
    }
    return updatedConfig;
}
/**
 * Gets the sorting field or (sorting type for box-plot) and orderBy from an input string.
 * @param stringToParse The input string to parse, like `orderByField` of a query clause or `orderDataBy` property of a chart config.
 * @param useDefault If set to `false`, the default sort order will not be used.
 * @returns
 */
export function getOrderByComponents(stringToParse, useDefault) {
    let orderByComponents = { fieldToSort: "" };
    if (!isEmptyStringField(stringToParse)) {
        const orderByString = stringToParse.trim();
        /*
        We are trying to detect if the field name contains spaces. If it does, we are getting the last space index
        to separate the field name from the sort order.
        */
        const parts = orderByString.split(" ");
        const last = parts.at(-1)?.toUpperCase();
        if (last === WebChartSortOrderKinds.Ascending || last === WebChartSortOrderKinds.Descending) {
            orderByComponents = {
                fieldToSort: orderByString.substring(0, orderByString.lastIndexOf(" ")),
                orderBy: last,
            };
        }
        else {
            // when the input string doesn't have a sort order, we are setting the default sort order to ascending
            // and treating the whole string as the field to sort
            orderByComponents = {
                fieldToSort: orderByString,
            };
            // unless the useDefault is specifically set to false, we are setting the default sort order to ascending
            if (useDefault !== false) {
                orderByComponents.orderBy = WebChartSortOrderKinds.Ascending;
            }
        }
    }
    return orderByComponents;
}
/**
 * Determines if certain chart subtype is a split by chart.
 * @param chartSubType The chart subtype
 * @returns true if the chart subtype is a split by chart, false otherwise
 */
export function isSplitByChart(chartSubType) {
    return (chartSubType === ChartSubType.BarAndLineSplitBy ||
        chartSubType === ChartSubType.BarAndLineSplitByNoAggregation ||
        chartSubType === ChartSubType.BoxPlotMonoFieldAndSplitBy ||
        chartSubType === ChartSubType.BoxPlotMultiFieldsAndSplitBy ||
        chartSubType === ChartSubType.BoxPlotMonoFieldAndCategoryAndSplitBy);
}
