import { RESTSimpleFillSymbolStyle, RESTSimpleLineSymbolStyle, RESTSymbolType } from "@arcgis/charts-spec";
import { cloneDeep } from "lodash-es";
import { getFeatureLayer } from "../index";
/**
 * Converts a hex color into a ESRI Color array ([number, number, number, number])
 * @param hex The hex code to convert
 */
function hexToRGB(hex) {
    let color;
    // applying a different code according to the length of the hex code
    switch (hex.length) {
        case 4: {
            // #123
            const matchResult = hex.match(/^#?([\da-f]{1})([\da-f]{1})([\da-f]{1})$/iu);
            if (matchResult) {
                color = [
                    Number.parseInt(`${matchResult[1]}${matchResult[1]}`, 16),
                    Number.parseInt(`${matchResult[2]}${matchResult[2]}`, 16),
                    Number.parseInt(`${matchResult[3]}${matchResult[3]}`, 16),
                    255
                ];
            }
            break;
        }
        case 5: {
            // #123C
            const matchResult = hex.match(/^#?([\da-f]{1})([\da-f]{1})([\da-f]{1})([\da-f]{1})$/iu);
            if (matchResult) {
                color = [
                    Number.parseInt(`${matchResult[1]}${matchResult[1]}`, 16),
                    Number.parseInt(`${matchResult[2]}${matchResult[2]}`, 16),
                    Number.parseInt(`${matchResult[3]}${matchResult[3]}`, 16),
                    Number.parseInt(`${matchResult[4]}${matchResult[4]}`, 16)
                ];
            }
            break;
        }
        case 7: {
            // #112233
            const matchResult = hex.match(/^#?([\da-f]{2})([\da-f]{2})([\da-f]{2})$/iu);
            if (matchResult) {
                color = [
                    Number.parseInt(matchResult[1], 16),
                    Number.parseInt(matchResult[2], 16),
                    Number.parseInt(matchResult[3], 16),
                    255
                ];
            }
            break;
        }
        case 9: {
            // #112233CC
            const matchResult = hex.match(/^#?([\da-f]{2})([\da-f]{2})([\da-f]{2})([\da-f]{2})$/iu);
            if (matchResult) {
                color = [
                    Number.parseInt(matchResult[1], 16),
                    Number.parseInt(matchResult[2], 16),
                    Number.parseInt(matchResult[3], 16),
                    Number.parseInt(matchResult[4], 16)
                ];
            }
            break;
        }
        default:
            break;
    }
    return color;
}
/**
 * Converts an input value typed as WebChartDataItemValue into a Color.
 * Only values representing a hex code or an ESRI color will yield a result.
 * @param inputColor The input value to convert to a color
 */
function convertColor(inputColor) {
    let outputColor;
    if (typeof inputColor === "string" && inputColor.startsWith("#")) {
        const rgbColor = hexToRGB(inputColor);
        if (rgbColor) {
            outputColor = rgbColor;
        }
    }
    else if (Array.isArray(inputColor) && inputColor.every((c) => typeof c === "number")) {
        // if the input is already matching the color type, we'll just return it
        if (inputColor.length === 4) {
            outputColor = inputColor;
        }
        // if the input misses the opacity we default it to 255
        else if (inputColor.length === 3) {
            outputColor = [...inputColor, 255];
        }
    }
    return outputColor;
}
/**
 * Type guard to verify whether a string is a SimpleFillSymbolStyle
 * @param style The style input to check
 */
function isSFSStyle(style) {
    return (style === RESTSimpleFillSymbolStyle.BackwardDiagonal ||
        style === RESTSimpleFillSymbolStyle.Cross ||
        style === RESTSimpleFillSymbolStyle.DiagonalCross ||
        style === RESTSimpleFillSymbolStyle.ForwardDiagonal ||
        style === RESTSimpleFillSymbolStyle.Horizontal ||
        style === RESTSimpleFillSymbolStyle.Null ||
        style === RESTSimpleFillSymbolStyle.Solid ||
        style === RESTSimpleFillSymbolStyle.Vertical);
}
/**
 * Type guard to verify whether a string is a SimpleLineSymbolStyle
 * @param style The style input to check
 */
function isSLSStyle(style) {
    return (style === RESTSimpleLineSymbolStyle.Dash ||
        style === RESTSimpleLineSymbolStyle.DashDot ||
        style === RESTSimpleLineSymbolStyle.DashDotDot ||
        style === RESTSimpleLineSymbolStyle.Dot ||
        style === RESTSimpleLineSymbolStyle.Null ||
        style === RESTSimpleLineSymbolStyle.Solid);
}
/**
 * Creates a chart renderer based on the provided config fields and inline data
 * @param props.categoryField The category field
 * @param props.numericFields The numeric fields
 * @param props.configFields The config fields
 * @param props.inlineData The data to convert
 */
export function convertConfigFieldsToChartRenderer(props) {
    const { categoryField, numericFields, configFields, inlineData } = props;
    let chartRenderer = {};
    // If there's at least one config field
    if (configFields && Object.keys(configFields).length > 0 && "dataItems" in inlineData) {
        chartRenderer = {
            type: "uniqueValue",
            field1: categoryField,
            uniqueValueInfos: []
        };
        // looping through the inline data to create for each item a unique value info to be added to the chart renderer
        inlineData.dataItems.forEach((dataItem) => {
            const xValue = dataItem[categoryField];
            if (xValue !== null && xValue !== undefined) {
                // the unique value info can have a label and a symbol (either SFS, SMS or SLS)
                let uniqueValueInfo = { value: xValue };
                // each data item can contain info for multiple y values so we loop through each one of them
                numericFields.forEach((y) => {
                    const fillColor = convertColor(dataItem[`${configFields.fillColor}_${y}`]);
                    const fillOpacity = dataItem[`${configFields.fillOpacity}_${y}`];
                    const fillStyle = dataItem[`${configFields.fillStyle}_${y}`];
                    const markerSize = dataItem[`${configFields.markerSize}_${y}`];
                    const outlineColor = convertColor(dataItem[`${configFields.outlineColor}_${y}`]);
                    const outlineOpacity = dataItem[`${configFields.outlineOpacity}_${y}`];
                    const outlineWidth = dataItem[`${configFields.outlineWidth}_${y}`];
                    const outlineStyle = dataItem[`${configFields.outlineStyle}_${y}`];
                    const lineColor = convertColor(dataItem[`${configFields.lineColor}_${y}`]);
                    const lineOpacity = dataItem[`${configFields.lineOpacity}_${y}`];
                    const lineWidth = dataItem[`${configFields.lineWidth}_${y}`];
                    const lineStyle = dataItem[`${configFields.lineStyle}_${y}`];
                    const displayLabel = dataItem[`${configFields.displayLabel}_${y}`];
                    if (typeof displayLabel === "string") {
                        uniqueValueInfo = {
                            ...uniqueValueInfo,
                            label: displayLabel
                        };
                    }
                    if (markerSize !== null && markerSize !== undefined) {
                        const markerSymbol = { type: RESTSymbolType.SMS };
                        if (markerSize && typeof markerSize === "number") {
                            markerSymbol.size = markerSize;
                        }
                        uniqueValueInfo.symbol = markerSymbol;
                    }
                    if (fillColor || isSFSStyle(fillStyle)) {
                        // if the marker symbol was created beforehand we'll use it to add the colors. Otherwise we create whole new fill symbol
                        const fillOrMarkerSymbol = uniqueValueInfo.symbol && uniqueValueInfo.symbol.type === RESTSymbolType.SMS
                            ? uniqueValueInfo.symbol
                            : {
                                type: RESTSymbolType.SFS
                            };
                        if (fillColor) {
                            if (fillOpacity && typeof fillOpacity === "number") {
                                fillColor[3] = fillOpacity;
                            }
                            fillOrMarkerSymbol.color = fillColor;
                        }
                        if (isSFSStyle(fillStyle)) {
                            fillOrMarkerSymbol.style = fillStyle;
                        }
                        uniqueValueInfo.symbol = fillOrMarkerSymbol;
                        if (outlineColor || (outlineWidth !== undefined && outlineWidth !== null) || isSLSStyle(outlineStyle)) {
                            const outlineSymbol = { type: RESTSymbolType.SLS };
                            if (outlineColor) {
                                if (outlineOpacity && typeof outlineOpacity === "number") {
                                    outlineColor[3] = outlineOpacity;
                                }
                                outlineSymbol.color = outlineColor;
                            }
                            if (isSLSStyle(outlineStyle)) {
                                outlineSymbol.style = outlineStyle;
                            }
                            if (outlineWidth && typeof outlineWidth === "number") {
                                outlineSymbol.width = outlineWidth;
                            }
                            uniqueValueInfo.symbol.outline = outlineSymbol;
                        }
                    }
                    else if (lineColor || (lineWidth !== undefined && lineWidth !== null) || isSLSStyle(lineStyle)) {
                        const lineSymbol = { type: RESTSymbolType.SLS };
                        if (lineColor) {
                            if (lineOpacity && typeof lineOpacity === "number") {
                                lineColor[3] = lineOpacity;
                            }
                            lineSymbol.color = lineColor;
                        }
                        if (isSLSStyle(lineStyle)) {
                            lineSymbol.style = lineStyle;
                        }
                        if (lineWidth !== undefined && lineWidth !== null && typeof lineWidth === "number") {
                            lineSymbol.width = lineWidth;
                        }
                        uniqueValueInfo.symbol = lineSymbol;
                    }
                });
                // if the symbol as at least a label or a symbol, we add it to the chart renderer
                if (uniqueValueInfo.label || uniqueValueInfo.symbol) {
                    chartRenderer.uniqueValueInfos.push(uniqueValueInfo);
                }
            }
        });
    }
    return chartRenderer;
}
/**
 * Removes the config fields from the feature set to add to the feature collection
 * @param numericFields The list of numeric fields
 * @param configFields The config fields
 * @param inlineData The data to cure
 */
function removeConfigFieldsFromData(props) {
    const { numericFields, configFields, inlineData } = props;
    const dataWithoutConfigFields = inlineData;
    if ("dataItems" in inlineData) {
        Object.entries(configFields).forEach(([_configFieldKey, configFieldValue]) => {
            numericFields.forEach((y) => {
                const configFieldName = `${configFieldValue}_${y}`;
                inlineData.dataItems.forEach((dataItem) => {
                    if (configFieldName in dataItem) {
                        delete dataItem[configFieldName];
                    }
                });
            });
        });
    }
    return dataWithoutConfigFields;
}
/**
 * Converts an array of inline data to a feature set and returns an IFeatureLayer
 * @param props.categoryField The category field
 * @param props.categoryType The category type
 * @param props.numericFields The numeric fields
 * @param props.inlineData The inline data to convert
 */
export function convertInlineDataToLayerDefinition(props) {
    const { categoryField, categoryType, numericFields, inlineData } = props;
    let iLayer;
    if ("dataItems" in inlineData) {
        const { dataItems } = inlineData;
        const fields = [];
        // building the features from the inline data
        let objectIdField = "OBJECTID";
        const features = dataItems.map((inlineDataItem, dataItemIndex) => {
            const attributes = { ...inlineDataItem };
            // adding an object id generated from the data item when missing
            if ("FID" in inlineDataItem) {
                objectIdField = "FID";
            }
            else if ("ObjectId" in inlineDataItem) {
                objectIdField = "ObjectId";
            }
            // If the object if is not part of the data item, we set it to the item index
            if (!attributes[objectIdField]) {
                attributes[objectIdField] = dataItemIndex;
            }
            return { attributes };
        });
        // Adding the object id field to the field list
        fields.push({ type: "esriFieldTypeOID", name: objectIdField });
        // Adding the x field to the field list
        let xFieldType;
        switch (categoryType) {
            case "date":
                xFieldType = "esriFieldTypeDate";
                break;
            case "number":
                xFieldType = "esriFieldTypeDouble";
                break;
            case "string":
            default:
                xFieldType = "esriFieldTypeString";
                break;
        }
        fields.push({ type: xFieldType, name: categoryField });
        // Adding the stat fields to the list
        numericFields.forEach((fieldName) => {
            fields.push({ type: "esriFieldTypeDouble", name: fieldName });
        });
        // building the IFeatureLayer to return
        iLayer = {
            id: "",
            layerType: "ArcGISFeatureLayer",
            featureCollection: {
                layers: [{ layerDefinition: { fields }, featureSet: { features } }]
            }
        };
    }
    return iLayer;
}
/**
 * Converts an array of inline data into a feature collection.
 * Also returns a chartRenderer if the configFields are provided.
 * @param props.categoryField The category field
 * @param props.categoryType The category type
 * @param props.numericFields The numeric fields
 * @param props.configFields The config fields
 * @param props.inlineData The inline data
 */
export async function convertInlineConfigToLayerConfig(props) {
    const { categoryField, categoryType, numericFields, configFields, inlineData } = props;
    let chartRenderer;
    let layer;
    let inlineDataToConvert = cloneDeep(inlineData);
    if (configFields) {
        // converting the config fields to a chart renderer
        chartRenderer = convertConfigFieldsToChartRenderer({
            categoryField,
            numericFields,
            configFields,
            inlineData: inlineDataToConvert
        });
        // removing the config fields from the data array
        inlineDataToConvert = removeConfigFieldsFromData({
            numericFields,
            configFields,
            inlineData: inlineDataToConvert
        });
    }
    // Creating the layer description
    const iLayer = convertInlineDataToLayerDefinition({
        categoryField,
        categoryType,
        numericFields,
        inlineData: inlineDataToConvert
    });
    if (iLayer) {
        layer = await getFeatureLayer(iLayer);
    }
    return { chartRenderer, iLayer, layer };
}
