import { RESTStatisticType } from "@arcgis/charts-spec";
import { queryAllFeatures } from "../../query-features";
import { getLayerFromQueryObject } from "../../feature-layer";
import { defaultBoxPlotCategory } from "../../defaults";
/**
 * Fetches the unique values from a given field.
 * /!\ Currently the results for a same split-by field might differ from a category field to another.
 * This is due to the server not being able to make a distinction in some cases between values having for
 * only difference trailing spaces (e.g. `value` and `value ` and `value  `). It means it will aggregate the
 * stats for those similar values.
 * Given the stats for the similar values will be later aggregated, we also want the unique value list to carry
 * the same approximation.
 * To get a list of unique values that will match the chart's data, we simulate the data query and retain the unique
 * values for given the split-by field.
 * @param props.queryObject The object used to query the features (layer or feature layer view)
 * @param props.queryConfig The query configuration
 * @param props.category The category
 * @param props.splitByField The split-by field
 * @param props.abortKey The key to abort the request
 */
export async function uniqueValues(props) {
    const { queryObject, queryConfig, category, splitByField, chartLimits, abortKey } = props;
    const isSplitByFieldProvided = splitByField !== undefined;
    // We add the category to the groupByFieldsForStatistics, except if the category is the placeholder used by the box plot
    // charts with no category.
    const groupByFieldsForStatistics = category !== defaultBoxPlotCategory ? [category] : [];
    // add the splitByField to the groupByFieldsForStatistics if it is defined
    if (isSplitByFieldProvided) {
        groupByFieldsForStatistics.push(splitByField);
    }
    const layer = getLayerFromQueryObject(queryObject);
    const modifiedQueryConfig = {
        ...queryConfig,
        groupByFieldsForStatistics: Array.from(new Set(groupByFieldsForStatistics)),
        outStatistics: [
            {
                statisticType: RESTStatisticType.Count,
                onStatisticField: layer.objectIdField,
                // Generating an out stat field name. Letting it to COUNT only can fail the query on arcObject layers
                outStatisticFieldName: `${RESTStatisticType.Count}_OBJECTID`,
            },
        ],
        orderByFields: undefined,
        // Adding num (resultRecordCount) to match the query fetching the results (build by `buildUniqueQuery()). We pick an arbitrary limit (maxBarThreePlusSeriesCountTotal)
        // as we don't know in advance how many results we will get.
        // TODO: once the servers are able to make a distinction between values with a different number of trailing spaces we'll be able to revert that function to its previous
        // state, using the `returnDistinctValues` on a specific field.
        // Refer to: https://devtopia.esri.com/WebGIS/arcgis-charts/issues/8208
        num: chartLimits?.maxBarThreePlusSeriesCountTotal,
    };
    // Executing the series of queries to get unique values from the field
    const results = (await queryAllFeatures({
        queryObject,
        featureCount: 1, // skipping the count
        queryConfig: modifiedQueryConfig,
        abortKey,
    }));
    // Mapping the results to provide an array of unique values
    // The array is sorted alphabetically or numerically according to the values type
    const uniqueValuesArray = Array.from(new Set(results.map((feature) => feature.attributes[isSplitByFieldProvided ? splitByField : category]))).sort((a, b) => (typeof a === "string" && typeof b === "string" ? a.localeCompare(b) : Number(a) - Number(b)));
    return uniqueValuesArray;
}
