import { RESTSymbolType, WebChartTypes } from "@arcgis/charts-spec";
import { getChartOrSeriesType } from "../misc";
/**
 * Check if the chart config supports rotation or not
 * @param config - the chart config
 */
export function isRotationSupported(config) {
    const rotationSupportedChartTypes = [
        WebChartTypes.BarSeries,
        WebChartTypes.LineSeries,
        WebChartTypes.ComboLineAndBarSeries,
        WebChartTypes.BoxPlotSeries,
    ];
    const chartType = getChartOrSeriesType(config?.series);
    return chartType !== undefined ? rotationSupportedChartTypes.includes(chartType) : false;
}
/**
 * Check if the chart config supports rotation or not
 * @param config - the chart config
 */
export function isStackedTypeSupported(config) {
    const stackedTypeSupportedChartTypes = [
        WebChartTypes.BarSeries,
        WebChartTypes.LineSeries,
        WebChartTypes.ComboLineAndBarSeries,
        WebChartTypes.RadarSeries,
    ];
    const chartType = getChartOrSeriesType(config?.series);
    return chartType !== undefined ? stackedTypeSupportedChartTypes.includes(chartType) : false;
}
/**
 * Type guard for ISimpleFillSymbol
 * @param symbol
 */
export function isSFS(symbol) {
    return symbol?.type === RESTSymbolType.SFS;
}
/**
 * Type guard for ISimpleLineSymbol
 * @param symbol
 */
export function isSLS(symbol) {
    return symbol?.type === RESTSymbolType.SLS;
}
/**
 * Type guard for ISimpleMarkerSymbol
 * @param symbol
 */
export function isSMS(symbol) {
    return symbol?.type === RESTSymbolType.SMS;
}
/**
 * Type guard for WebChartTextSymbol
 * @param symbol
 */
export function isWebChartTextSymbol(symbol) {
    return symbol?.type === RESTSymbolType.TS;
}
export function hasILayer(config) {
    return config && "iLayer" in config && config.iLayer;
}
/**
 * Determines if the provided `config` is the specified chart type `T`.
 * @param config The chart config to check.
 * @param chartType The chart type to check against.
 */
export function isChartType(config, chartType) {
    return config !== undefined && chartType !== undefined && config.series[0]?.type === chartType;
}
/**
 * Determines if the series is a box plot series.
 * @param series The series to check.
 */
export function isBoxPlotSeries(series) {
    return series.type === WebChartTypes.BoxPlotSeries;
}
/**
 * Determines if the series is a line chart series.
 * @param series The series to check.
 */
export function isLineChartSeries(series) {
    return series.type === WebChartTypes.LineSeries;
}
/**
 * Determines if the series array is of type box plot series.
 * @param series The series to check.
 */
export function isBoxPlotSeriesArray(series) {
    return series.every(isBoxPlotSeries);
}
/**
 * Type guard for axis format being NumberFormatOptions
 * @param valueFormat - valueFormat to check
 */
export function isNumberFormatOptions(valueFormat) {
    return valueFormat !== undefined && valueFormat.type === WebChartTypes.NumberAxisFormat;
}
