import { defaultPieFromFieldsName, defaultPieFromFieldsValue, GaugeChartSubTypes, WebChartStackedKinds, WebChartTypes, } from "@arcgis/charts-spec";
import { extent as d3Extent } from "d3-array";
import { boxPlotOutliersSuffix, boxPlotZScoreSuffix } from "../defaults";
import { getChartSubType, getSplitByValue } from "../feature-layer";
import { getStackedType, getChartOrSeriesType } from "../misc";
import { ChartSubType } from "../misc/interfaces";
import { BoxPlotYFieldKeys } from "./interfaces";
import { isNil } from "lodash-es";
/**
 * Generates the y field names for box plot
 * @param y
 * @returns
 */
export function getBoxPlotStatisticFieldNames(y) {
    const emptyY = y === undefined || y === "";
    // we add the `y` value as a prefix when provided
    return {
        [BoxPlotYFieldKeys.Average]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.Average}`,
        [BoxPlotYFieldKeys.Min]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.Min}`,
        [BoxPlotYFieldKeys.Max]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.Max}`,
        [BoxPlotYFieldKeys.FirstQuartile]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.FirstQuartile}`,
        [BoxPlotYFieldKeys.Median]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.Median}`,
        [BoxPlotYFieldKeys.ThirdQuartile]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.ThirdQuartile}`,
        [BoxPlotYFieldKeys.IQR]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.IQR}`,
        [BoxPlotYFieldKeys.Outlier]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.Outlier}`,
        [BoxPlotYFieldKeys.StandardDeviation]: `${!emptyY ? `${y}_` : ""}${BoxPlotYFieldKeys.StandardDeviation}`,
    };
}
/**
 * Check the `subType` in the chart config to determine whether the chart is a gauge by feature.
 * If `subType` is not defined, the chart is defaulted to be a statistic gauge.
 *
 * @param config the chart config
 * @default false
 */
export function isGaugeByFeature(config) {
    return config.subType === GaugeChartSubTypes.FeatureGauge;
}
/**
 * From fetched data (type: WebChartDataTypes) returns the part containing the data that
 * is to be assigned to the chart (WebChartDataItem[] | WebChartBin[])
 * @param props.fetchedData The fetched finished data
 */
export function extractChartDataFromFetchedData(fetchedData) {
    let chartData;
    if (fetchedData !== undefined) {
        if ("bins" in fetchedData) {
            chartData = fetchedData.bins;
        }
        else if ("dataItems" in fetchedData) {
            chartData = fetchedData.dataItems;
        }
    }
    return chartData;
}
/**
 * Determine whether the current box plot has multi-fields or not based on its query type
 * @param chartSubType: Box plot sub type
 */
export function isMultiFieldsBoxPlot(chartSubType) {
    return (chartSubType === ChartSubType.BoxPlotMultiFields ||
        chartSubType === ChartSubType.BoxPlotMultiFieldsAndCategory ||
        chartSubType === ChartSubType.BoxPlotMultiFieldsAndSplitBy);
}
/**
 * Compute Y value for given Box Plot Y field key based on the provided config
 * @param props.key The BoxPlotYFieldKeys related to the stat
 * @param props.y The y field to use to get the y value
 * @param props.chartSubType The box plot sub type
 * @param props.splitBy The where clause used as a split-by value for a given series
 * @param props.splitByValue The split-by value for a given series
 * @param props.showOutliers Whether to show the outliers
 * @param props.standardizeValues Whether to show the standardized values
 */
export function getBoxPlotYValue(props) {
    const { key, y, chartSubType, splitBy, splitByValue, showOutliers, standardizeValues, } = props;
    let normalizedSplitByValue = splitByValue;
    const yFieldNames = getBoxPlotStatisticFieldNames(y);
    let propName = yFieldNames[key];
    // adding the outliers for the suffix (added earlier in the workflow by `fetchBoxPlotAlternativeData()`)
    // only the min and max stats are to be considered
    if (showOutliers === true &&
        (key === BoxPlotYFieldKeys.Min || key === BoxPlotYFieldKeys.Max)) {
        propName = `${propName}${boxPlotOutliersSuffix}`;
    }
    // append z_score to the y value
    if (standardizeValues === true &&
        (isMultiFieldsBoxPlot(chartSubType) || key === BoxPlotYFieldKeys.Outlier)) {
        propName = `${propName}${boxPlotZScoreSuffix}`;
    }
    // only extract split-by value if we don't already provide splitByValue argument to the function
    if (splitByValue === undefined || `${splitByValue}`.trim() === "") {
        normalizedSplitByValue = getSplitByValue({
            where: splitBy,
            normalize: true,
            escape: false,
        });
    }
    // we need to prepend splitByValue to box plots with splitByField
    if (normalizedSplitByValue !== undefined &&
        `${normalizedSplitByValue}`.trim() !== "") {
        propName = `${normalizedSplitByValue}${propName !== "" ? `_${propName}` : ""}`;
    }
    return propName;
}
/**
 * Check if the Box-plot data item has valid open or close (first quartile/third quartile) values since those values are considered as necessary.
 * @param props.dataItem The WebChartDataItem to check for valid values
 * @param props.y The y value
 * @param props.splitBy The split-by value
 * @param props.showOutliers Whether the outliers are shown
 */
export function doesBoxPlotItemHaveValidQuartiles(props) {
    const { dataItem, y, splitBy, showOutliers } = props;
    const firstQuartile = getBoxPlotYValue({
        key: BoxPlotYFieldKeys.FirstQuartile,
        y,
        splitBy,
        showOutliers,
    });
    const thirdQuartile = getBoxPlotYValue({
        key: BoxPlotYFieldKeys.ThirdQuartile,
        y,
        splitBy,
        showOutliers,
    });
    return (Number.isFinite(dataItem[firstQuartile]) ||
        Number.isFinite(dataItem[thirdQuartile]));
}
/**
 * Indicated whether the provided data item is valid or not for different box plot types
 * @param props.configSeries The list of series in the chart config
 * @param props.boxPlotDataItem The data item for a bot plot series in the config
 * @param props.yList The list of y values
 * @param props.showOutliers Whether the outliers are shown
 * @returns A boolean
 */
function isBoxPlotDataItemValid(props) {
    const { configSeries, boxPlotDataItem, yList, showOutliers } = props;
    const chartSubType = getChartSubType(configSeries);
    let isValidData = false;
    switch (chartSubType) {
        case ChartSubType.BoxPlotMonoField:
        case ChartSubType.BoxPlotMultiFields:
        case ChartSubType.BoxPlotMonoFieldAndCategory:
            // testing only open/close (first quartile/third quartile) for box plot, considering those values as necessary
            isValidData = doesBoxPlotItemHaveValidQuartiles({
                dataItem: boxPlotDataItem,
                showOutliers,
            });
            break;
        case ChartSubType.BoxPlotMultiFieldsAndCategory:
            isValidData = yList.some((y) => doesBoxPlotItemHaveValidQuartiles({
                dataItem: boxPlotDataItem,
                y,
                showOutliers,
            }));
            break;
        case ChartSubType.BoxPlotMonoFieldAndSplitBy:
        case ChartSubType.BoxPlotMultiFieldsAndSplitBy:
        case ChartSubType.BoxPlotMonoFieldAndCategoryAndSplitBy:
            isValidData = configSeries.some((boxPlotSeries) => doesBoxPlotItemHaveValidQuartiles({
                dataItem: boxPlotDataItem,
                splitBy: boxPlotSeries.query?.where,
                showOutliers,
            }));
            break;
        default:
            isValidData = false;
            break;
    }
    return isValidData;
}
function getAllSeriesIDs(seriesConfig) {
    return seriesConfig
        .map((currentSeries) => currentSeries.id)
        .filter((id) => id !== "undefined");
}
/**
 * Gathers the list of `y` values used by a chart from the series configs.
 * For non-aggregated split-by bar/line charts, the y values are constructed from the series's where clauses.
 *
 * Notes:
 * 1. If the list of visible series id is provided it will be used to filter out the series, otherwise they will all be considered.
 * 2. The returned y list should be independently from any prefix or suffix that are used for other features like
 * color match, selection, tooltips, etc.
 * @param seriesConfig The array of configs for the chart series.
 * @param filteredSeriesIds List of series ids to use as a filter.
 */
export function getYList(seriesConfig, filteredSeriesIds) {
    // we return an empty list if no input is provided or there's no y value found
    let yList = [];
    const seriesIds = filteredSeriesIds ?? getAllSeriesIDs(seriesConfig);
    const chartSubType = getChartSubType(seriesConfig);
    // looking for y in the series configs
    yList = seriesConfig
        .filter((currentSeries) => seriesIds.includes(currentSeries.id))
        .map((currentSeries) => {
        let y = "y" in currentSeries ? currentSeries.y : undefined;
        // Specifically for non-aggregated split-by bar/line charts only, the y values are constructed from the series's where clauses.
        if (y !== undefined &&
            chartSubType === ChartSubType.BarAndLineSplitByNoAggregation) {
            y = `${y.toString()}_${getSplitByValue({
                where: currentSeries.query?.where,
            })}`;
        }
        return y;
    })
        .filter((value) => value !== undefined);
    // returning a list from which we exclude duplicates
    return Array.from(new Set(yList.flat()));
}
/**
 * Gathers the list of `y` values used by a box plot from the series configs.
 * @param props.showMean Whether the box plot mean lines are shown
 * @param props.seriesConfig The array of configs for the chart series.
 * @param props.filteredSeriesIds List of series ids to use as a filter.
 */
export function getBoxPlotYList(props) {
    const { seriesConfig, filteredSeriesIds, showMean } = props;
    // we return an empty list if no input is provided or there's no y value found
    let yList = [];
    // when showMean is true we only use the first box plot series
    if (showMean === true) {
        const seriesReference = seriesConfig.find((s) => s.type === WebChartTypes.BoxPlotSeries);
        if (seriesReference !== undefined && "y" in seriesReference) {
            yList = [seriesReference.y].flat();
        }
    }
    else {
        yList = getYList(seriesConfig, filteredSeriesIds);
    }
    return yList;
}
/**
 * Indicates whether a stat is a valid number.
 * @param value The value to test
 * @param nullAsValid Whether to consider `null` values as valid
 */
function isValidStat(value, nullAsValid) {
    return (value === null && nullAsValid === true) || Number.isFinite(value);
}
/**
 * validate the individual bar chart data item using its x and y values
 * @param props.dataItem The bar/line chart data item
 * @param props.x The x value from series config
 * @param props.y The y value from series config
 * @param props.nullAsValid Whether to consider `null` values as valid
 */
export function validateBarNLineChartDataItem(props) {
    const { dataItem, x, y, nullAsValid = false } = props;
    return (dataItem[x] !== undefined &&
        (typeof dataItem[x] !== "number" || Number.isFinite(dataItem[x])) &&
        isValidStat(dataItem[y], nullAsValid));
}
/**
 * Indicates whether a data set is empty (no bar, no slice, etc.) and contains at least one valid item.
 * @param props.config The chart's config
 * @param props.nullAsValid Whether to consider `null` values as valid
 * @param props.dataSet The chart's data
 * @returns A boolean
 */
export function isEmptyDataset(props) {
    const { chartConfig, nullAsValid = false, dataSet } = props;
    let empty = true;
    if (chartConfig !== undefined) {
        const chartType = getChartOrSeriesType(chartConfig.series);
        if (dataSet === undefined || dataSet.length === 0) {
            empty = true;
        }
        else {
            const yList = getYList(chartConfig.series);
            // if the data set is not empty we look for a valid element. If there's any, we consider the set empty
            empty = !dataSet.some((dataItem) => {
                let validData = false;
                switch (chartType) {
                    case WebChartTypes.HistogramSeries:
                        {
                            const bin = dataItem;
                            // histogram: checking if min and max values are valid
                            validData =
                                isValidStat(bin.minValue, nullAsValid) &&
                                    isValidStat(bin.maxValue, nullAsValid);
                        }
                        break;
                    case WebChartTypes.ScatterSeries:
                        {
                            const marker = dataItem;
                            const { x, y } = chartConfig
                                .series[0];
                            // scatter plot: checking if x and y are valid
                            validData =
                                isValidStat(marker[x], nullAsValid) &&
                                    isValidStat(marker[y], nullAsValid);
                        }
                        break;
                    case WebChartTypes.ComboLineAndBarSeries:
                    case WebChartTypes.RadarSeries:
                    case WebChartTypes.LineSeries:
                    case WebChartTypes.BarSeries: {
                        const barLineChartDataItem = dataItem;
                        const { x } = chartConfig.series[0];
                        // bar/line chart: checking if the category or x value is valid, as the y value
                        validData = yList.some((y) => validateBarNLineChartDataItem({
                            dataItem: barLineChartDataItem,
                            x,
                            y,
                            nullAsValid,
                        }));
                        break;
                    }
                    case WebChartTypes.BoxPlotSeries: {
                        const configSeries = chartConfig.series;
                        const { showOutliers } = chartConfig;
                        validData = isBoxPlotDataItemValid({
                            configSeries,
                            boxPlotDataItem: dataItem,
                            yList,
                            showOutliers,
                        });
                        break;
                    }
                    case WebChartTypes.PieSeries:
                        {
                            const pieChartDataItem = dataItem;
                            let { x, y } = chartConfig.series[0];
                            if (getChartSubType(chartConfig.series) ===
                                ChartSubType.PieFromFields) {
                                x = defaultPieFromFieldsName;
                                y = defaultPieFromFieldsValue;
                            }
                            const yValue = pieChartDataItem[y];
                            // pie chart: checking if the x (category / field name) and y value are valid
                            validData =
                                pieChartDataItem[x] !== undefined &&
                                    pieChartDataItem[x] !== null &&
                                    !isNil(yValue) &&
                                    isValidStat(Number(yValue), nullAsValid);
                        }
                        break;
                    case WebChartTypes.GaugeSeries:
                        {
                            const { x } = chartConfig.series[0];
                            const gaugeDataItem = dataItem;
                            // gauge: checking if the x value is valid
                            validData = isValidStat(gaugeDataItem[x], nullAsValid);
                        }
                        break;
                    default:
                        break;
                }
                return validData;
            });
        }
    }
    return empty;
}
/**
 * Gathers the list of `y` values used by a chart, grouped by series type.
 * If the list of visible series id is provided it will be used to filter out the series, otherwise they will all be considered.
 * @param seriesConfig The config for the series
 * @param filteredSeriesIds List of series ids to use as a filter
 */
function getYListPerSeriesType(seriesConfig, filteredSeriesIds) {
    const yListPerSeriesType = new Map();
    const seriesIds = filteredSeriesIds ?? getAllSeriesIDs(seriesConfig);
    seriesConfig.forEach((currentSeries) => {
        if (seriesIds.includes(currentSeries.id) && "y" in currentSeries) {
            const currentYList = yListPerSeriesType.get(currentSeries.type) ?? [];
            currentYList.push(currentSeries.y);
            yListPerSeriesType.set(currentSeries.type, currentYList);
        }
    });
    return yListPerSeriesType;
}
/**
 * Gets the data min and max values for a stacked series
 * @param seriesConfig The config for the series
 * @param filteredSeriesIds List of series ids to use as a filter
 * @param filteredData The data to look into
 */
function getDataMinMaxFromStackedSeries(props) {
    const { seriesConfig, filteredSeriesIds, filteredData } = props;
    const yListPerSeriesType = getYListPerSeriesType(seriesConfig, filteredSeriesIds);
    // line series can be stacked in two different ways, impacting the min/max results
    // we fetch the `stackNegativeValuesToBaseline` from the first line series we find
    const stackNegativeValuesToBaseline = seriesConfig.find((currentSeries) => currentSeries.type === WebChartTypes.LineSeries)?.stackNegativeValuesToBaseline ?? true;
    let stackedMaxY = Number.MIN_SAFE_INTEGER;
    let stackedMinY = Number.MAX_SAFE_INTEGER;
    filteredData.forEach((dataItem, itemIndex) => {
        // going through each series type, as they are not stacked together (ex: bar series and line series are overlapping each other)
        yListPerSeriesType.forEach((yList) => {
            if (!stackNegativeValuesToBaseline) {
                let currentSum = 0;
                for (let iY = 0; iY < yList.length; iY += 1) {
                    const currentYValue = dataItem[yList[iY]];
                    currentSum += currentYValue;
                    stackedMaxY = Math.max(stackedMaxY, currentSum);
                    stackedMinY = Math.min(stackedMinY, currentSum);
                }
            }
            else {
                // In stack mode + `stackNegativeValuesToBaseline` = true the columns / bullets are split in 2 groups: the positive values together and the negative values together
                let sumPerPositiveCategoryOnly = [];
                let sumPerNegativeCategoryOnly = [];
                for (let iY = 0; iY < yList.length; iY += 1) {
                    const currentYValue = dataItem[yList[iY]];
                    if (currentYValue < 0) {
                        sumPerNegativeCategoryOnly[itemIndex] =
                            (sumPerNegativeCategoryOnly[itemIndex] ?? 0) + currentYValue;
                    }
                    else {
                        sumPerPositiveCategoryOnly[itemIndex] =
                            (sumPerPositiveCategoryOnly[itemIndex] ?? 0) + currentYValue;
                    }
                }
                sumPerPositiveCategoryOnly = sumPerPositiveCategoryOnly
                    .filter((value) => Number.isFinite(value))
                    .sort((a, b) => a - b);
                sumPerNegativeCategoryOnly = sumPerNegativeCategoryOnly
                    .filter((value) => Number.isFinite(value))
                    .sort((a, b) => a - b);
                // if there are positive values, we calculated data max based on it
                stackedMaxY = Math.max(stackedMaxY, sumPerPositiveCategoryOnly.length > 0
                    ? sumPerPositiveCategoryOnly[sumPerPositiveCategoryOnly.length - 1]
                    : sumPerNegativeCategoryOnly[sumPerNegativeCategoryOnly.length - 1]);
                // if there are negative values, we calculated data min based on it
                stackedMinY = Math.min(stackedMinY, sumPerNegativeCategoryOnly.length > 0
                    ? sumPerNegativeCategoryOnly[0]
                    : sumPerPositiveCategoryOnly[0]);
            }
        });
    });
    return { stackedMinY, stackedMaxY };
}
function findSpecificBoxPlotDataMinMax(props) {
    const { dataItem, ...boxPlotYValueProps } = props;
    const minFieldName = getBoxPlotYValue({
        key: BoxPlotYFieldKeys.Min,
        ...boxPlotYValueProps,
    });
    const maxFieldName = getBoxPlotYValue({
        key: BoxPlotYFieldKeys.Max,
        ...boxPlotYValueProps,
    });
    const min = dataItem[minFieldName];
    const max = dataItem[maxFieldName];
    return { min, max };
}
/**
 * Looks for the min and max values from a data set, considering only the visible series.
 * @param props.chartConfig The chart config
 * @param props.chartData The chart's data (as fed to the amCharts chart)
 * @param props.filteredSeriesIds List of series ids to use as a filter
 */
export function getDataMinMaxFromSeries(props) {
    let minY = Number.NaN;
    let maxY = Number.NaN;
    let minX;
    let maxX;
    const { chartConfig, chartData, filteredSeriesIds } = props;
    const { series: seriesConfig } = chartConfig;
    if (seriesConfig?.length > 0) {
        const { x } = seriesConfig[0];
        // filtering out the `null` categories
        const filteredData = chartData.filter((dataItem) => dataItem[x] !== null);
        [minX, maxX] = d3Extent(filteredData, (dataItem) => dataItem[x]);
        const isDateAxis = chartConfig.axes?.[1]?.valueFormat.type === WebChartTypes.DateAxisFormat;
        if (isDateAxis && minX !== undefined && maxX !== undefined) {
            [minX, maxX] = [new Date(minX).valueOf(), new Date(maxX).valueOf()];
        }
        const chartType = getChartOrSeriesType(chartConfig.series);
        // Box plot: find the min and max values in the data
        if (chartType === WebChartTypes.BoxPlotSeries) {
            minY = Number.MAX_SAFE_INTEGER;
            maxY = Number.MIN_SAFE_INTEGER;
            const yList = getYList(seriesConfig, filteredSeriesIds);
            const chartSubType = getChartSubType(seriesConfig);
            const standardizeValues = "standardizeValues" in chartConfig
                ? chartConfig.standardizeValues === true
                : undefined;
            // Get split-by values. Only include value if series is `visible`.
            const splitByValues = chartConfig.series.map((s) => s.visible !== false ? getSplitByValue({ where: s.query?.where }) : "");
            // If the array is empty (not using split-by) then add a blank value to make sure the split-by for loop below still runs once for each numeric field.
            if (splitByValues.length === 0) {
                splitByValues.push(" ");
            }
            for (let iData = 0; iData < filteredData.length; iData += 1) {
                for (let iY = 0; iY < yList.length; iY += 1) {
                    const y = chartSubType === ChartSubType.BoxPlotMultiFieldsAndCategory
                        ? yList[iY]
                        : undefined;
                    for (let iSplitBy = 0; iSplitBy < splitByValues.length; iSplitBy += 1) {
                        const { min, max } = findSpecificBoxPlotDataMinMax({
                            y,
                            splitByValue: splitByValues[iSplitBy],
                            chartSubType,
                            showOutliers: false,
                            standardizeValues,
                            dataItem: filteredData[iData],
                        });
                        if (min !== undefined && min !== null && min < minY) {
                            minY = min;
                        }
                        if (max !== undefined && max !== null && max > maxY) {
                            maxY = max;
                        }
                    }
                }
            }
        }
        else {
            const stackedType = getStackedType(chartConfig);
            switch (stackedType) {
                case WebChartStackedKinds.Stacked100: {
                    // NaN will let AmCharts calculate automatically the bounds
                    minY = Number.NaN;
                    maxY = Number.NaN;
                    break;
                }
                case WebChartStackedKinds.Stacked: {
                    const { stackedMinY, stackedMaxY } = getDataMinMaxFromStackedSeries({
                        seriesConfig,
                        filteredData,
                        filteredSeriesIds,
                    });
                    minY = stackedMinY;
                    maxY = stackedMaxY;
                    break;
                }
                case WebChartStackedKinds.Side:
                default: {
                    minY = Number.MAX_SAFE_INTEGER;
                    maxY = Number.MIN_SAFE_INTEGER;
                    const yList = getYList(seriesConfig, filteredSeriesIds);
                    // In Side by side mode we look for the min and max values in the dataset
                    for (let iY = 0; iY < yList.length; iY += 1) {
                        const currentY = yList[iY];
                        const [currentSeriesMin, currentSeriesMax] = d3Extent(filteredData, (dataItem) => dataItem[currentY]);
                        if (currentSeriesMin !== undefined && currentSeriesMin < minY) {
                            minY = currentSeriesMin;
                        }
                        if (currentSeriesMax !== undefined && currentSeriesMax > maxY) {
                            maxY = currentSeriesMax;
                        }
                    }
                    break;
                }
            }
        }
    }
    return { minY, maxY, minX, maxX };
}
