/**
 * Returns the ArcGIS server's rest/info url of the feature server given a feature layer url.
 * @example   getServerRestInfo(https://servicesdev.arcgis.com/b5ADkBof6gCHCFQm/arcgis/rest/services/Steph_Curry/FeatureServer/2
 * returns https://servicesdev.arcgis.com/b5ADkBof6gCHCFQm/arcgis/rest/info
 * @param {string} resUrl
 * @returns {string}
 */
function getServerRestInfo(resUrl) {
    const agsRest = "/rest/services";
    let lcResUrl = resUrl.toLowerCase();
    // look for '/rest/services'
    const idx = lcResUrl.indexOf(agsRest);
    lcResUrl = idx > -1 ? resUrl.substring(0, idx) : resUrl;
    return `${lcResUrl}/rest/info`;
}
/**
 * Returns a boolean to indicate if the feature layer is on standard feature data store or not.
 * https://doc.arcgis.com/en/arcgis-online/reference/faq.htm#anchor7
 * @param {FeatureLayer} layer
 * @returns {Promise<boolean>}
 */
export async function isStandardLayer(layer) {
    // starting with default true when we cannot determine the feature layer data store.
    let isStandard = true;
    // All Hosted items have arcgis domain in the url.
    const onlineDomain = ".arcgis.com/";
    const isOnlineFS = layer.url?.includes(onlineDomain);
    if (isOnlineFS &&
        getMainLayer(layer).sourceJSON.serviceItemId !== undefined) {
        const restInfoUrl = getServerRestInfo(layer.url);
        try {
            const esriRequest = await importRequest();
            const serverRestInfo = (await esriRequest(restInfoUrl, { query: { f: "json" } })).data;
            if (serverRestInfo.owningTenant !== undefined &&
                serverRestInfo.owningSystemUrl !== undefined) {
                const portalSelfUrl = `${serverRestInfo.owningSystemUrl}/sharing/rest/portals/${serverRestInfo.owningTenant}`;
                const portalSelf = (await esriRequest(portalSelfUrl, {
                    authMode: "auto",
                    query: { f: "json" },
                })).data;
                const portal = await newPortalPortal({ sourceJSON: portalSelf });
                await portal.load();
                // dataStoreLevel only defined for premium data stores.
                isStandard =
                    portal?.sourceJSON?.subscriptionInfo?.dataStoreLevel === undefined;
            }
        }
        catch (e) {
            // continue regardless of error
        }
    }
    return isStandard;
}
/**
 * Indicates whether the bar or line series is configured to use time binning.
 * @param props.seriesConfig The series configuration
 * @param props.xAxisValueFormatType The x-axis value format type
 * @param props.setTimeBinningInfoWhenNotProvided Boolean indicating whether we want to set the time binning info when not provided
 */
export function isSeriesUsingTimeBinning(props) {
    const { seriesConfig, xAxisValueFormatType, setTimeBinningInfoWhenNotProvided, } = props;
    let decision = false;
    // Only for:
    // - bar or line series
    // - using a date axis
    // - and when `binTemporalData` is not set to false
    if ((seriesConfig.type === WebChartTypes.LineSeries ||
        seriesConfig.type === WebChartTypes.BarSeries) &&
        seriesConfig.binTemporalData !== false &&
        xAxisValueFormatType === WebChartTypes.DateAxisFormat) {
        const chartSubType = getChartSubType([
            seriesConfig,
        ]);
        // Only if an aggregation is set
        if (chartSubType !== ChartSubType.BarAndLineNoAggregation &&
            chartSubType !== ChartSubType.BarAndLineSplitByNoAggregation) {
            const { timeIntervalUnits, timeIntervalSize } = seriesConfig;
            // Only if the interval unit and size are provided, or if `setTimeBinningInfoWhenNotProvided` is set to true
            decision =
                setTimeBinningInfoWhenNotProvided === true ||
                    (timeIntervalSize !== undefined &&
                        timeIntervalSize > 0 &&
                        timeIntervalUnits !== undefined);
        }
    }
    return decision;
}
/**
 * Finds the matching field name for a given layer based on the fieldToMatch parameter.
 * @param {QueryObject} queryObject
 * @param {string} fieldToMatch
 * @returns {Promise<string>}
 */
export async function findMatchingLayerFieldName(queryObject, fieldToMatch) {
    const layerFieldList = await fetchLayerFieldsInfo(queryObject);
    // we look for a match in 2 steps:
    // 1. Perfect match: the case of the fields is exactly the same. This helps when the layer has fields with the same name but with a
    // different case (e.g. "field" and "FiElD"). Refer to https://devtopia.esri.com/WebGIS/arcgis-charts/issues/8305
    // 2: Approximate match: we upper-case the fields to compare them
    let match = layerFieldList.find((field) => fieldToMatch === field.name);
    if (!match) {
        match = layerFieldList.find((field) => fieldToMatch.toUpperCase() === field.name.toUpperCase());
    }
    const matchingField = match?.name ?? fieldToMatch;
    return matchingField;
}
/**
 * Indicates whether a feature layer supports queries with a `distance` parameter
 * @param layer The layer to test
 * @returns A boolean
 */
export function doesLayerSupportQueryWithDistance(layer) {
    return (layer.capabilities?.query?.supportsDistance ||
        getMainLayer(layer).sourceJSON?.advancedQueryCapabilities
            ?.supportsQueryWithDistance === true);
}
/**
 * Computes a feature layer capabilities regarding its spatial aggregation statistics.
 * Only considers the 'EnvelopeAggregate' for now.
 * @param layer The layer instance
 * @returns A sum up of the capabilities
 */
export function getLayerSpatialAggregationStatisticsCapabilities(layer) {
    let compatibleWithEnvelopeAggregate = false;
    const supportedSpatialAggregationStatisticsJSON = getMainLayer(layer).sourceJSON?.advancedQueryCapabilities
        ?.supportedSpatialAggregationStatistics ?? [];
    const supportedSpatialAggregationStatistics = layer.capabilities?.query.supportedSpatialAggregationStatistics ?? [];
    compatibleWithEnvelopeAggregate =
        supportedSpatialAggregationStatisticsJSON.includes("EnvelopeAggregate") ||
            (supportedSpatialAggregationStatistics.envelope !== undefined &&
                supportedSpatialAggregationStatistics.envelope);
    return compatibleWithEnvelopeAggregate;
}
/**
 * Builds a WebChartQuery based on information coming from the runtime filters (chart options), the config and the data source.
 * The filters by attributes from all sources are all combined together (using the AND operator).
 * Regarding the other filters (by geometry, gdb version and time extent) only one of each can be applied. The runtime
 * filters are chosen over the config filters.
 * @param props.queryObject The query object (feature layer or feature layer view)
 * @param props.chartConfig The chart's config
 * @param props.runtimeDataFilters The runtime data filters
 * @param props.ignoreViewExtent Whether to ignore the view extent
 */
export function buildQueryConfig(props) {
    const { queryObject, chartConfig, runtimeDataFilters, ignoreViewExtent } = props;
    const { series: seriesConfig, dataFilters } = chartConfig;
    const { where: configFilter, gdbVersion: configGdbVersion, timeExtent: configTimeExtent, geometry: configGeometry, distance: configDistance, units: configUnits, } = dataFilters ?? {};
    const { where: runtimeFilter, gdbVersion: runtimeGdbVersion, timeExtent: runtimeTimeExtent, geometry: runtimeGeometry, distance: runtimeDistance, units: runtimeUnits, } = runtimeDataFilters ?? {};
    const firstSeriesConfig = seriesConfig[0];
    const { query: seriesQuery } = firstSeriesConfig;
    const { where: seriesQueryFilter } = seriesQuery ?? {};
    const chartSubType = getChartSubType(seriesConfig);
    const queryConfig = { ...seriesQuery };
    // For the filter by attribute, we aggregate all the provided filters
    queryConfig.where = combineFilters([configFilter, runtimeFilter]);
    // we use also the filter at the series query filter but on split-by charts for which
    // it is reserved to identify the split-by field and value
    if (chartSubType !== ChartSubType.BarAndLineSplitBy &&
        chartSubType !== ChartSubType.BoxPlotMonoFieldAndSplitBy &&
        chartSubType !== ChartSubType.BoxPlotMonoFieldAndCategoryAndSplitBy &&
        chartSubType !== ChartSubType.BoxPlotMultiFieldsAndSplitBy &&
        chartSubType !== ChartSubType.BarAndLineSplitByNoAggregation) {
        queryConfig.where = combineFilters([queryConfig.where, seriesQueryFilter]);
    }
    // we use the orderByFields from the chartConfig if it is defined
    if (chartConfig.orderOptions?.orderByFields !== undefined) {
        queryConfig.orderByFields = chartConfig.orderOptions.orderByFields;
    }
    // For the other filters, we chose the runtime filter over the config filter
    queryConfig.gdbVersion = runtimeGdbVersion ?? configGdbVersion;
    queryConfig.timeExtent = getQueryTimeExtent(queryObject, runtimeTimeExtent ?? configTimeExtent);
    queryConfig.geometry = getQueryGeometry({
        queryObject,
        defaultGeometry: runtimeGeometry ?? configGeometry,
        ignoreViewExtent,
    });
    queryConfig.distance = runtimeDistance ?? configDistance;
    queryConfig.units = runtimeUnits ?? configUnits;
    // We apply the distinct values only if the aggregation type is count
    if (seriesQuery?.outStatistics?.[0]?.statisticType === RESTStatisticType.Count) {
        queryConfig.returnDistinctValues =
            seriesQuery?.returnDistinctValues ?? false;
    }
    return queryConfig;
}
